.profileBackground{
    background-image: url("../../images/profilepic4.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    width: 100%;
    height: 300px;
}

.totalFollowerHeader, .totalFollowingHeader,
.totalLikesHeader, .totalRecipesHeader {
    text-align: center;
    width: 100%;
    font-family: 'EB Garamond', serif;
    font-size: 3em;
    margin-bottom: .5em;
}


